* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  height: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.container h1 {
  text-align: center;
  font-family: "Righteous", cursive;
  font-size: 2rem;
  -webkit-animation: head-animate 5s linear infinite;
          animation: head-animate 5s linear infinite;
}

@-webkit-keyframes head-animate {
  0%,
  100% {
    color: yellowgreen;
    text-shadow: 1px 1px 2px yellowgreen;
    text-transform: uppercase;
  }
  50% {
    color: #111;
    text-shadow: 0 0 10px none;
    text-transform: lowercase;
  }
}

@keyframes head-animate {
  0%,
  100% {
    color: yellowgreen;
    text-shadow: 1px 1px 2px yellowgreen;
    text-transform: uppercase;
  }
  50% {
    color: #111;
    text-shadow: 0 0 10px none;
    text-transform: lowercase;
  }
}

.container .loader {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container .loader span {
  height: 20px;
  width: 20px;
  display: inline-block;
  border-radius: 50%;
  transition: all 0.5s;
  -webkit-animation: animate 2s infinite;
          animation: animate 2s infinite;
}

.container .loader span:nth-child(1) {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.container .loader span:nth-child(2) {
  -webkit-animation-delay: 0.25s;
          animation-delay: 0.25s;
}

.container .loader span:nth-child(3) {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

.container .loader span:nth-child(4) {
  -webkit-animation-delay: 0.75s;
          animation-delay: 0.75s;
}

.container .loader span:nth-child(5) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.container .loader span:nth-child(6) {
  -webkit-animation-delay: 1.25s;
          animation-delay: 1.25s;
}

@-webkit-keyframes animate {
  0% {
    border: 1px solid #333;
    background-color: yellowgreen;
    box-shadow: 1px 1px 2px yellowgreen;
    transform: translateY(0);
  }
  50% {
    border: 1px solid #333;
    background-color: #333;
    box-shadow: 1px 1px 2px yellowgreen;
    transform: translateY(-25px);
  }
  100% {
