    transform: translateY(0);
  }
  50% {
    border: 1px solid #333;
    background-color: #333;
    box-shadow: 1px 1px 2px yellowgreen;
    transform: translateY(-25px);
  }
  100% {
    border: 1px solid #333;
    background-color: yellowgreen;
    box-shadow: 1px 1px 2px yellowgreen;
    transform: translateY(0);
  }
}
